import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {NavOutletService} from '../../router/nav-outlet/nav-outlet.service';
import {ComponentNavPage} from '../../app.routing';
import {NavOutletPage} from '../../models/nav-outlet-page';

@Component({
  selector: 'app-vault-profile-signin-code',
  templateUrl: './vault-profile-signin-code.component.html',
  styleUrls: ['./vault-profile-signin-code.component.scss']
})
export class VaultProfileSigninCodeComponent implements OnInit, NavOutletPage {
  loading: boolean = false
  errorMsg: string = ""

  @Input() currentEmail = "test@example.com"

  constructor(
    public changeRef: ChangeDetectorRef,
    private navOutletService: NavOutletService,
    private authService: AuthService,
  ) {}

  ngOnInit(): void {
  }
  markForCheck(){
    this.changeRef.markForCheck()
  }

  onCodeCompleted(code: string){
    this.loading = true

    console.log("submit finish", this.currentEmail, code)
    this.authService.VaultAuthFinish(this.currentEmail, code)
      .then(() => {
        this.loading = false
        this.navOutletService.navigateByUrl(ComponentNavPage.Dashboard)
      })
      .catch((err)=>{
        this.loading = false
        if(err?.name){
          this.errorMsg = "code is incorrect"
        } else{
          this.errorMsg = "an unknown error occurred during sign-in"
        }
        // const toastNotification = new ToastNotification()
        // toastNotification.type = ToastType.Error
        // toastNotification.message = this.errorMsg
        // this.toastService.show(toastNotification)
      })
  }

}
