import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {VaultService} from '../../services/vault.service';
import {NavOutletService} from '../../router/nav-outlet/nav-outlet.service';
import {ComponentNavPage} from '../../app.routing';
import {NavOutletPage} from '../../models/nav-outlet-page';

@Component({
  selector: 'app-identity-verification',
  templateUrl: './identity-verification.component.html',
  styleUrls: ['./identity-verification.component.scss']
})
export class IdentityVerificationComponent implements OnInit, NavOutletPage {
  loading = false;

  constructor(
    public changeRef: ChangeDetectorRef,
    private vaultService: VaultService,
    private navOutletService: NavOutletService,
  ) { }

  ngOnInit(): void {
  }
  markForCheck(){
    this.changeRef.markForCheck()
  }

  verifyIdentity(){
    this.loading = true
    this.vaultService.verificationWithPopup().subscribe((result)=>{
      this.loading = false
      console.log("verification result", result)
      this.navOutletService.navigateByUrl(ComponentNavPage.Dashboard)
    }, (err)=>{
      this.loading = false
      console.error("verification error", err)
    })
  }
}
