/**
 * AppConfig interface. Update this interface to strictly type your config
 * object.
 */
import {ApiMode} from '../app.constants';
import {Organization} from './fasten/organization';
import {VaultProfile} from './fasten/vaultProfile';
import {SearchFilter} from './search-filter';
import {
  LighthouseBrandListDisplayItem,
  LighthouseEndpointListDisplayItem,
  LighthousePortalListDisplayItem
} from './lighthouse/lighthouse-source-search';
import {RecordLocatorFacility} from './fasten/record-locator-facility';

export interface VaultSystemConfig {
  publicId: string;
  apiMode: ApiMode;
  org?: Organization;
  org_id?: string
  reconnectOrgConnectionId?: string;
}

export class VaultProfileConfig {
  vaultProfile?: VaultProfile;

  // connected patient accounts
  connectedPatientAccounts?: RecordLocatorFacility[];

  // pending patient accounts?
  pendingPatientAccounts?: { brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem }[];
  addPendingAccount(brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem): void {
    if (!this.pendingPatientAccounts) {
      this.pendingPatientAccounts = []
    }
    this.pendingPatientAccounts?.push({brand, portal, endpoint})
  }
  addConnectedAccount(recordLocatorFacility: RecordLocatorFacility): void {
    if (!this.connectedPatientAccounts) {
      this.connectedPatientAccounts = []
    }
    this.connectedPatientAccounts?.push(recordLocatorFacility)
  }
}


export interface VaultSearchConfig {
  searchFilter?: SearchFilter
  selectedBrand?: LighthouseBrandListDisplayItem
}

// - apiMode
// - organization information
// - org credential information
// - reconnect id.

/**
 * Default configuration for the entire application. This object is used by
 * "PortalConfigService" to set the default configuration.
 */
export const vaultConfigDefaults: VaultSystemConfig = {
  publicId: '',
  apiMode: ApiMode.Test
};
