import {Directive, ElementRef, Input, ViewContainerRef} from '@angular/core';
import {NavOutletService} from './nav-outlet.service';

@Directive({
  selector: '[navOutletLink]'
})
export class NavOutletLinkDirective {

  constructor(
    private eleRef: ElementRef,
    public navOutletService: NavOutletService
  ) {}

  @Input() set navOutletLink(nextPage: string) {
    this.navOutletService.navigateByUrl(nextPage)
  }
}
