import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {VaultProfile} from '../../models/fasten/vaultProfile';
import {AuthService} from '../../services/auth.service';
import {NavOutletService} from '../../router/nav-outlet/nav-outlet.service';
import {ComponentNavPage} from '../../app.routing';
import {NavOutletPage} from '../../models/nav-outlet-page';

@Component({
  selector: 'app-auth-signin',
  templateUrl: './vault-profile-signin.component.html',
  styleUrls: ['./vault-profile-signin.component.scss']
})
export class VaultProfileSigninComponent implements OnInit, NavOutletPage {
  loading: boolean = false
  showMessage: boolean = false

  submitted: boolean = false
  existingVaultProfile: VaultProfile = new VaultProfile()
  errorMsg: string = ""
  message: string = ""

  constructor(
    public changeRef: ChangeDetectorRef,
    private authService: AuthService,
    private navOutletService: NavOutletService,
  ) { }

  ngOnInit(): void {
    const urlParams = new URLSearchParams(window.location.search);
    this.setMessage(urlParams.get('action') || "")
  }
  markForCheck(){
    this.changeRef.markForCheck()
  }

  signinSubmit(){
    this.submitted = true;
    this.loading = true

    console.log("Signin", this.existingVaultProfile.email)
    this.authService.VaultAuthBegin(this.existingVaultProfile.email!)
      .then(() => {
        this.loading = false
        this.navOutletService.navigateByUrl( ComponentNavPage.VaultProfileSigninCode, new Map<string,any>([["currentEmail", this.existingVaultProfile.email]]))
      })
      .catch((err)=>{
        this.loading = false
        if(err?.name){
          this.errorMsg = "email or password is incorrect"
        } else{
          this.errorMsg = "an unknown error occurred during sign-in"
        }
        // const toastNotification = new ToastNotification()
        // toastNotification.type = ToastType.Error
        // toastNotification.message = this.errorMsg
        // this.toastService.show(toastNotification)
      })
  }

  setMessage(action: string) {
    if (action === "email-changed"){
      this.showMessage = true;
      this.message = "Email successfully changed! Please sign in with your new email.";
    } else if (action === "password-changed"){
      this.showMessage = true;
      this.message = "Password successfully changed! Please sign in with your new password.";
    }
  }
}
