import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {VaultSystemConfig, vaultConfigDefaults, VaultSearchConfig, VaultProfileConfig} from '../models/vault-config';
import {merge} from 'lodash';
import {ApiMode} from '../app.constants';
import {VaultProfile} from '../models/fasten/vaultProfile';
import {Organization} from '../models/fasten/organization';
import {VaultService} from './vault.service';
import {
  LighthouseBrandListDisplayItem, LighthouseEndpointListDisplayItem,
  LighthousePortalListDisplayItem
} from '../models/lighthouse/lighthouse-source-search';
import {RecordLocatorFacility} from '../models/fasten/record-locator-facility';
import {NavOutletRoute} from '../app.routing';

@Injectable({
  providedIn: 'root'
})
export class ConfigService {
  //TODO: there should be 3 types of "config" settings:
  // 1. Service settings
  // - apiMode
  // - organization information
  // - org credential information
  // - reconnect id.
  // 2. User settings
  // - user token/cookie data
  // - vault profile
  // - current connected patient auth's
  // - (FUTURE) potential patient auths?
  // 3. Search settings
  // - search filters
  // - selected brand
  // -

  //public but should only be used in HTML directly. (via `| async`)
  public systemConfigSubject: BehaviorSubject<VaultSystemConfig>;
  public vaultProfileConfigSubject: BehaviorSubject<VaultProfileConfig>;
  public searchConfigSubject: BehaviorSubject<VaultSearchConfig>;


  private _defaultSystemConfig: VaultSystemConfig;

  constructor() {
    this._defaultSystemConfig = vaultConfigDefaults
    this.systemConfigSubject = new BehaviorSubject(this._defaultSystemConfig);
    this.vaultProfileConfigSubject = new BehaviorSubject(Object.setPrototypeOf({}, VaultProfileConfig.prototype));
    this.searchConfigSubject = new BehaviorSubject({} as VaultSearchConfig);

  }


  // -----------------------------------------------------------------------------------------------------
  // @ Accessors
  // -----------------------------------------------------------------------------------------------------

  /**
   * Setter & getter for config
   */

  //Setter
  set systemConfig(value: Partial<VaultSystemConfig>) {
    // get the current config, merge the new values, and then submit.
    const mergedSettings = merge({}, this.systemConfigSubject.getValue(), value);

    // Store the value
    if(JSON.stringify(mergedSettings) !== JSON.stringify(this.systemConfigSubject.getValue())){
      console.log('updating system settings:', mergedSettings)
      this.systemConfigSubject.next(mergedSettings);
    }
  }

  //Getter
  get systemConfig$(): VaultSystemConfig {
    console.log('getting cached system settings:', this.systemConfigSubject.getValue())
    return this.systemConfigSubject.getValue()
  }

  //Setter
  set vaultProfileConfig(value: Partial<VaultProfileConfig>) {
    // get the current config, merge the new values, and then submit.
    const mergedSettings = merge({}, this.vaultProfileConfigSubject.getValue(), value);

    // Store the value
    if(JSON.stringify(mergedSettings) !== JSON.stringify(this.vaultProfileConfigSubject.getValue())){
      console.log('updating vault profile settings:', mergedSettings)
      this.vaultProfileConfigSubject.next(Object.setPrototypeOf(mergedSettings, VaultProfileConfig.prototype));
    }
  }
  //Getter
  get vaultProfileConfig$(): VaultProfileConfig {
    console.log('getting vault profile settings:', this.vaultProfileConfigSubject.getValue())
    return this.vaultProfileConfigSubject.getValue()
  }
  vaultProfileAddPendingAccount(brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem){
    let updatedVaultProfile = this.vaultProfileConfig$
    updatedVaultProfile.addPendingAccount(brand, portal, endpoint)
    this.vaultProfileConfig = updatedVaultProfile
  }
  vaultProfileAddConnectedAccount(recordLocatorFacility: RecordLocatorFacility){
    let updatedVaultProfile = this.vaultProfileConfig$
    updatedVaultProfile.addConnectedAccount(recordLocatorFacility)
    this.vaultProfileConfig = updatedVaultProfile
  }

  //Setter
  set searchConfig(value: Partial<VaultSearchConfig>) {
    // get the current config, merge the new values, and then submit.
    const mergedSettings = merge({}, this.searchConfigSubject.getValue(), value);

    // Store the value
    if(JSON.stringify(mergedSettings) !== JSON.stringify(this.searchConfigSubject.getValue())){
      console.log('updating search settings:', mergedSettings)
      this.searchConfigSubject.next(mergedSettings);
    }
  }
  //Getter
  get searchConfig$(): VaultSearchConfig {
    console.log('getting search settings:', this.searchConfigSubject.getValue())
    return this.searchConfigSubject.getValue()
  }
}
